import { ChangeDetectorRef, Component, OnInit, Renderer2, ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { UserRoleService } from 'app/shared/services/user/user-role.service';
import { RoleService } from 'app/shared/services/user/role.service';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { ClientService } from 'app/shared/services/client/client.service';
import { UserService } from 'app/shared/services/user/user.service';
import { CpoService } from 'app/shared/services/cpo/cpo.service';

function autocompleteObjectValidator(): ValidatorFn {
    return (control: AbstractControl): { [key: string]: any } | null => {
        if (typeof control.value === 'string') {
            return { 'invalidAutocompleteObject': { value: control.value } }
        }
        return null  /* valid option selected */
    }
}
@Component({
    selector: 'app-create',
    templateUrl: './create.component.html',
    styleUrls: ['./create.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations: fuseAnimations
})
export class CreateComponent implements OnInit {

    form: FormGroup;
    filteredOptionsClient: Observable<any[]>;
    filteredOptionsCpo: Observable<any[]>;
    filteredOptionsUser: Observable<any[]>;
    filteredOptionsRole: Observable<any[]>;
    clients: any;
    cposName: any;
    users: any;
    stations: any;
    station_id: any;
    default_station: any;
    localStorage: any;
    radiobuttonList: any;
    currentCheckedValue = null;
    messageConstant: MessageConstant;
    @ViewChild("toggleElement") ref: any;
    status = true;
    selectedStation: any;
    defaultSelectedStation: any;
    default_checkedStation: any;
    constructor(private fb: FormBuilder, private snackBar: MatSnackBar, private router: Router, private cpoService: CpoService,
        private clientService: ClientService, private userService: UserService, private roleService: RoleService,private cdr:ChangeDetectorRef) {

    }

    ngOnInit() {
        this.initialize();
        this.getClients();
        // this.getUserList();
        //this.getRoleList();
    }

    initialize() {
        this.localStorage = JSON.parse(localStorage.getItem('userDetails'));

        this.form = this.fb.group({
            client: ['', [autocompleteObjectValidator(), Validators.required]],
            cpos: ['', [autocompleteObjectValidator(), Validators.required]],
            userList: ['', [autocompleteObjectValidator(), Validators.required]],
            stationList: ['', [Validators.required]],
            defaultStation: ['', [Validators.required]],
            status: [{ value: false }, [Validators.required]]
        });
    }

    onSubmit() {
        if (this.form.valid) {
            let formValues = this.form.value;

            let params = {
                user_id: formValues.userList.id,
                station_data: this.radiobuttonList.map(v => ({ station_id: v.station_id, created_date: v.created_date, create_by: v.createdby, modify_date: v.modify_date, modify_by: v.modifyby, default_station: v.default_station })),
                status: this.status == true ? 'Y' : 'N',
                created_by: this.localStorage.userId,
                modify_by: this.localStorage.userId
            }

            this.userService.UserStationMappingV1(params).subscribe(
                (resp: any) => {
                    if (resp.status == true) {
                        this.snackBar.open(MessageConstant.userStationAssign, 'Dismiss', { duration: 5000 });
                        this.router.navigate(['userManagement/manageUserStation/crud']);
                    } else {
                        this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
                    }

                },
                (err) => {
                    this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
                }
            )
        } else {
            return;
        }
    }

    getErrorMessage(form: FormGroup) {
        return form.get('email').hasError('required')
            ? 'You must enter a value'
            : form.get('email').hasError('email')
                ? 'Not a valid email'
                : '';
    }
    updateStatus(event) {
        this.status = event.checked;
        this.ref.checked = this.status;
    }

    getClients() {
        this.clientService.getActiveClientsCW(this.localStorage.userId).subscribe(
            (data: any) => {
                this.clients = data;
                const toSelect = this.clients.find(c => c.id == this.localStorage.client_id);
                this.form.get('client').setValue(toSelect);
                this.getCPOS(toSelect.id);
                this.getUserList(toSelect.id);
                this.filteredOptionsClient = this.form.controls.client.valueChanges
                    .pipe(
                        startWith(''),
                        map(value => typeof value === 'string' ? value : value.name),
                        map(name => name ? this._filterClientModels(name) : this.clients)
                    );
            }, (err) => {

            }
        )
    }
    onClientChange(client) {
        this.getCPOS(client.id);
        this.getUserList(client.id);
        this.form.get('cpos').setValue("");
        this.form.get('userList').setValue("");
        this.form.get('stationList').setValue("");
        this.radiobuttonList = [];
        this.cdr.detectChanges();
    }
    onCPOChange(cpo){
        this.form.get('userList').setValue("");
        this.form.get('stationList').setValue("");
        this.radiobuttonList = [];
        this.cdr.detectChanges();
    }

    displayClient(item: any): string {
        return item && item.name ? item.name : '';
    }

    private _filterClientModels(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.clients.filter(option => option.name.toLowerCase().includes(filterValue));
    }

    getCPOS(client_id) {
        this.cpoService.getCpoByClientId(client_id).subscribe(
            (data: any) => {
                this.cposName = data;
                this.filteredOptionsCpo = this.form.controls.cpos.valueChanges
                    .pipe(
                        startWith(''),
                        map(value => typeof value === 'string' ? value : value.name),
                        map(name => name ? this._filterCharCPO(name) : this.cposName)
                    );
            }, (err) => { }
        )
    }
    displayCpo(item: any): string {
        return item && item.name ? item.name : '';
    }
    private _filterCharCPO(name: string): any[] {
        const filterValue = name.toLowerCase();
        return this.cposName.filter(option => option.name.toLowerCase().includes(filterValue));
    }

    getUserList(id) {
        this.userService.getActiveUsersByClient(id).subscribe(
            (resp: any) => {
                this.users = resp.data;
                this.filteredOptionsUser = this.form.controls.userList.valueChanges
                    .pipe(
                        startWith(''),
                        map(value => typeof value === 'string' ? value : value.username),
                        map(username => username ? this._filterUserModels(username) : this.users)
                    );
            }, (err) => {
            }
        )
    }

    displayUser(item: any): string {
        return item && item.username ? item.username : '';
    }

    private _filterUserModels(username: string): any[] {
        const filterValue = username.toLowerCase();
        return this.users.filter(option => option.username.toLowerCase().includes(filterValue));
    }



    onStationChange(event) {
        this.radiobuttonList = event.value;
    }

    radioChange(e) {
        for (var i = 0; i < this.radiobuttonList.length; i++) {
            if (e.value == this.radiobuttonList[i].station_id) {
                this.radiobuttonList[i].default_station = 'Y'

            } else {
                this.radiobuttonList[i].default_station = 'N'

            }
        }
    }
    onUserChange(user) {
        this.userService.getStationListWithStationAssignedToUser(this.form.controls.client.value.id, user.id).subscribe(
            (resp: any) => {
                if (resp.status != 'false') {
                    this.stations = resp.data;
                    var selectedList = [];
                    for (let i = 0; i < this.stations.length; i++) {
                        if (this.stations[i].default_station == 'Y') {
                            this.form.get('defaultStation').setValue(this.stations[i]);
                        }
                        var userStation = this.stations[i];
                        if (userStation.map_id != null && userStation.map_id != undefined) {
                            selectedList.push(userStation);
                        }
                    }
                    this.form.get('stationList').setValue(selectedList);
                    this.radiobuttonList = selectedList;
                }
            }, (err) => {  }
        )
    }



}


