import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManageUserStationCrudComponent } from './manage-user-station-crud/manage-user-station-crud.component';
import { TABS_DEMO_ROUTES } from './manage-user-station-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ManageUserStationCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
    
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ManageUserStationRoutingModule{}